import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import './App.css';
import { ChatFeed, Message } from 'react-chat-ui'
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import SendIcon from '@material-ui/icons/Send';
import SecurityIcon from '@material-ui/icons/ExitToApp';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import withMobileDialog from '@material-ui/core/withMobileDialog';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import axios from 'axios';
import socketIOClient from "socket.io-client";
import LinearProgress from '@material-ui/core/LinearProgress';
import CircularProgress from '@material-ui/core/CircularProgress';
import MusicNoteIcon from '@material-ui/icons/MusicNote';
const styles = {
  chat_box: {
    paddingTop: 10,
    paddingLeft: 5, 
    paddingRight: 5
  },
  root: {
    flexGrow: 1,
  },
  flex: {
    flex: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
  paper_chat: {
    height: '75vh',
    paddingLeft: 5,
    paddingRight: 5,
    overflow: 'scroll'
  },
  paper_send: {
    padding: 10
  }
};
class App extends React.Component {
  state = {
    token: null,
    is_typing: false,
    open: true,
    messages: [],
    value: 'bonnie',
    loading: false,
    password: '',
    messagesLoading: true,
    textMessage: ''
  }
  handleChange = event => {
    this.setState({ value: event.target.value });
  };
  handleChangePass = prop => event => {
    this.setState({ [prop]: event.target.value });
  };
  authorize = () => {
    this.setState({loading: true});
    let toSend = {
      user: this.state.value, 
      password: this.state.password
    };
    var bodyFormData = new FormData();
    bodyFormData.set('user', this.state.value);
    bodyFormData.set('password', this.state.password);

    axios.post('/auth', toSend)
    .then( (response) => {
      if(response.data && !response.data.error) {
        sessionStorage.setItem('token', response.data.token);
        this.socket = socketIOClient("/");
        this.socket.emit('connection request', JSON.stringify({ user: this.state.value, token: response.data.token }));
        this.configureSocket();
        this.setState({ open: false, token: response.data.token, password: '' });
      } else {
        this.setState({loading: false});
      }
    })
    .catch( (err) => {
      this.setState({loading: false});
    });
  }
  socket = null;
  configureSocket = () => {
    this.socket.on('am i connected', (data) => {
      data = JSON.parse(data);
      if(data.error) {
        console.log('error connecting with the network');
        window.location.reload();
      } else {
        console.log('successfully connected');
        this.parseAndStoreData(data.data);
        this.setState({messagesLoading: false});
      }
    });
    this.socket.on('got message', (data) => {
      this.parseAndStoreData(data);
    });
    this.socket.on('play sound', (data) => {
      document.getElementById('audioPlayer').play();
    });
  }

  sendMessage = () => {
    if(this.state.textMessage !== ''){
      this.socket.emit('send message', JSON.stringify({ user: this.state.value, message: this.state.textMessage }));
      this.setState({ textMessage: '' })
    }
  }

  parseAndStoreData = (data) => {
    let i;
    let messages = [];
    for(i=0;i<data.length;i++) {
      messages.push( new Message({ 
        id: data[i].user === this.state.value ? 0 : 1,
        message: data[i].message
       }) )
    }
    this.setState({ messages });
    document.getElementById('dope').scrollTop = document.getElementById('dope').scrollHeight;
  }

  componentWillMount() {
    let token = sessionStorage.getItem('token');
    
    if (token) {
      console.log(token);
      let toSend = {
        token
      };
      this.setState({loading: true});
      axios.post('/verify', toSend)
      .then( (response) => {
        if(response.data && !response.data.error) {
          sessionStorage.setItem('token', response.data.token);
          this.socket = socketIOClient("/");
          this.socket.emit('connection request', JSON.stringify({ user: this.state.value, token: response.data.token }));
          this.configureSocket();
          this.setState({ open: false, token: response.data.token, password: '' });
        } else {
          sessionStorage.removeItem('token');
          this.setState({loading: false});
        }
      })
      .catch( () => {
        sessionStorage.removeItem('token');
        this.setState({loading: false});
      })
    }
  }
  handleClose = () => {
    this.setState({ open: false });
  };
  logout = () => {
    sessionStorage.removeItem('token');
    window.location.reload();
  }
  playSound = () => {
    this.socket.emit('play sound');
  }
  render() {
    const { classes } = this.props;
    const { fullScreen } = this.props;

    return (
      <div>
        <Dialog
          fullScreen={fullScreen}
          open={this.state.open}
          onClose={this.handleClose}
          aria-labelledby="responsive-dialog-title"
        >
          
          <DialogTitle id="responsive-dialog-title">{"If you dont know why you are here, you should probably leave!"}</DialogTitle>
          <DialogContent>
            <DialogContentText>
              Able was I saw Elba
            </DialogContentText>
            
            <RadioGroup
              aria-label="gender"
              name="gender1"
              className={classes.group}
              value={this.state.value}
              onChange={this.handleChange}
            >
              <FormControlLabel value="bonnie" control={<Radio />} label="Bonnie" />
              <FormControlLabel value="clyde" control={<Radio />} label="Clyde" />
            </RadioGroup>
            <TextField
              id="name"
              label="Password"
              className={classes.textField}
              value={this.state.password}
              onChange={this.handleChangePass('password')}
              margin="normal"
              fullWidth
              type="password"
            />
            { this.state.loading && <CircularProgress className={classes.progress}/>}
          </DialogContent>
          
          <DialogActions>
            <Button disabled={this.state.loading} onClick={this.authorize} variant="raised" color="primary">
              Authorize
            </Button>
          </DialogActions>
        </Dialog>
        <div className={classes.root}>
          
          <AppBar position="static">
            <Toolbar>
              <Typography variant="title" color="inherit" className={classes.flex}>
                Reach Me
              </Typography>
              <IconButton onClick={this.playSound}>
                <MusicNoteIcon style={{ color: 'white' }}/>
              </IconButton>
              <IconButton onClick={this.logout}>
                <SecurityIcon style={{ color: 'white' }}/>
              </IconButton>
            </Toolbar>
          </AppBar>
          <Grid className={classes.chat_box} container>
            <Grid item xs={12}>
              <Paper id='dope' className={classes.paper_chat}>
                <ChatFeed
                  messages={this.state.messages} // Boolean: list of message objects
                  isTyping={this.state.is_typing} // Boolean: is the recipient typing
                  hasInputField={false} // Boolean: use our input, or use your own
                  showSenderName // show the name of the user who sent the message
                  bubblesCentered={false} //Boolean should the bubbles be centered in the feed?
                  // JSON: Custom bubble styles
                  bubbleStyles={
                    {
                      text: {
                        fontSize: 20
                      },
                      chatbubble: {
                        borderRadius: 70,
                        padding: 15
                      }
                    }
                  }
                />
                <audio id='audioPlayer' hidden='true' ref="audio_tag" src="horn.mp3"/>
              </Paper>
            </Grid>
            <Grid item xs={12}>
              { this.state.messagesLoading &&  <LinearProgress /> }
              <Paper className={classes.paper_send}>
                <TextField
                  id="text-message"
                  label="Send a message"
                  fullWidth
                  value={this.state.textMessage}
                  onChange={this.handleChangePass('textMessage')}
                  InputProps={{
                    endAdornment: <InputAdornment position="end">
                      <IconButton onClick={this.sendMessage}>
                        <SendIcon/>
                      </IconButton>
                    </InputAdornment>,
                  }}
                  margin="normal"
                />
              </Paper>
            </Grid>
          </Grid>
        </div>
      </div>
    )
  }
}
App.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withMobileDialog()(withStyles(styles)(App));



